}
.timeline-steps .timeline-content {
  width: 12rem;
  text-align: center;
}
.timeline-steps .timeline-content .num {
  position: absolute;
  display: inline-flex;
  left: 89px;
  top: 6px;
  color: #fff;
  font-size: 1.5rem;
}
.timeline-steps .timeline-content .inner-circle {
  border-radius: 5rem;
  height: 3rem;
  width: 3rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: #9BC715;
}
.timeline-steps .timeline-content .inner-circle:before {
  content: "";
  background-color: #9BC715;
  display: inline-block;
  height: 4rem;
  width: 4rem;
  min-width: 4rem;
  border-radius: 7.25rem;
  opacity: 0.5;
}

@media (min-width: 768px) {
  .timeline-steps .timeline-step:not(:last-child):after {
    content: "";
    display: block;
    border-top: 0.25rem dotted #9BC715;
    width: 5rem;
    position: absolute;
    left: 9.1rem;
    top: 1.2125rem;
  }
  .timeline-steps .timeline-step:not(:first-child):before {
    content: "";
    display: block;
    border-top: 0.25rem dotted #9BC715;
    width: 6rem;
    position: absolute;
    right: 9.1rem;
    top: 1.2125rem;
  }
}
