.hover-text-one figure {
  position: relative;
  z-index: 1;
  display: inline-block;
  overflow: hidden;
  text-align: center;
}
.hover-text-one figure img {
  position: relative;
  display: block;
  min-height: 100%;
  z-index: 10;
}
.hover-text-one figure figcaption {
  padding-left: 0.4em;
  text-transform: uppercase;
  font-size: 0.68em;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  z-index: 20;
}
.hover-text-one figure figcaption {
  position: absolute;
  top: 4%;
  left: 0;
  width: 100%;
  height: 100%;
}
.hover-text-one figure .goods {
  top: 18%;
  left: -10px;
}
.hover-text-one figure .machine {
  top: 4%;
  left: -20px;
}
.hover-text-one figure .timeline {
  position: absolute;
  width: 100%;
  left: -30%;
  margin: 0;
  padding: 0;
  text-align: center;
  opacity: 0;
  transition: opacity 0.35s ease;
  z-index: 20;
}

figure.effect-text-three img {
  transition-delay: 0.4s;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s ease-in-out;
  transition: opacity 0.35s, transform 0.35s ease-in-out;
  transform: translate3d(0, 0, 0);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
figure.effect-text-three .combo {
  transition-delay: 0.4s;
  -webkit-transition: scale 0.35s, -webkit-transform 0.35s ease-in-out;
  transition: scale 0.35s, transform 0.35s ease-in-out;
}
figure.effect-text-three:hover img {
  transition-delay: 0.4s;
  -webkit-transform: translate3d(60px, 0, 0);
  transform: translate3d(60px, 0, 0);
}
figure.effect-text-three:hover ul {
  transition-delay: 0.4s;
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
figure.effect-text-three:hover .timeline {
  transition-delay: 0.4s;
  opacity: 1;
}
figure.effect-text-three:hover .combo {
  transition-delay: 0.4s;
  -webkit-transform: translate3d(70px, 0, 0) scale(0.75);
  transform: translate3d(70px, 0, 0) scale(0.75);
}
figure.effect-text-three figcaption {
  text-align: left;
}
figure.effect-text-three ul {
  transition-delay: 0.4s;
  padding-top: 1.5em;
  opacity: 0;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s ease-in-out;
  -webkit-transform: translate3d(30%, 0, 0);
  transform: translate3d(30%, 0, 0);
}

